import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { RegisterService } from '../register.service';
declare var jquery:any;
declare var $:any;

@Component({
	providers: [RegisterService],
	selector: 'app-register',
	templateUrl: './register.component.html',
	styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit, OnDestroy {
	data: {
		userName:	string,
		userPwd:	string,
		userEmail:	string,
	}
	registerResponse: {
		userName: string,
        userNameExists: boolean,
        userPwd: string,
        userEmail: string,
        userEmailExists: boolean,
        registerResult: string,
        registerError: Array<string>
	};
	connectionError: string;
	constructor(private RegisterService: RegisterService) { 
		this.data = {
			userName : '',
			userPwd : '',
			userEmail : '',
			};
		this.registerResponse = {
			userName: '',
			userNameExists: false,
			userPwd: '',
			userEmail: '',
			userEmailExists: false,
			registerResult: '',
			registerError: []
		}
		this.connectionError = '';
	}
	showPopOver(input: string, message: string) {
		$(input).popover('dispose');
		$(input).popover({content:message, trigger:'manual'});
		$(input).popover('show');
	}
	sendRegistration(userName:string, userPwd:string, userEmail:string){
		this.data.userName = userName;
		this.data.userPwd = userPwd;
		this.data.userEmail = userEmail;
		this.RegisterService.sendData(this.data).subscribe(
			(_res) => {
				this.registerResponse = _res;				
			},
			(error) => { 
				this.connectionError = 'Connection error, no server response.';
				this.showPopOver('#registerButton', this.connectionError);
			},
			() => {
				$('#register1, #register2, #register3, #registerButton').popover('dispose');
				if(this.registerResponse.registerResult =='REGISTER_ERROR') {
					console.log('RegisterERROR');
					if(this.registerResponse.userNameExists) this.showPopOver('#register1', 'Username already in use.');			
					if(this.registerResponse.userEmailExists) this.showPopOver('#register2', 'E-mail already in use.');
					if(this.registerResponse.registerError[0]=='User validation failed'){
						this.registerResponse.registerError.forEach((_err)=>{
							if(_err.includes('valid e-mail')) this.showPopOver('#register2', _err);
							if(_err.includes('password')) this.showPopOver('#register3', _err);
							if(_err.includes('user')) this.showPopOver('#register1', _err);
							if(_err.includes('e-mail')) this.showPopOver('#register2', _err);											
						});	
					}
				}
			});		
	}
	ngOnDestroy() {
		$('.popover').remove();
	}
	ngOnInit() {}
}



