import { Component, OnInit } from '@angular/core';
import { CurrentUserService } from '../current-user.service';
import { LogoutService } from '../logout.service';
import { Router } from '@angular/router';

@Component({
  providers: [ LogoutService ],
  selector: 'logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.css', '../app.component.css']
})
export class LogoutComponent implements OnInit {
	appUser: string;
	constructor(private CurrentUserService: CurrentUserService, private LogoutService: LogoutService, private Router: Router) { 
		this.appUser = this.CurrentUserService.currentUser;
	}
	logOut(){
		console.log(this.appUser);
		this.LogoutService.endSession({userName: this.appUser}).subscribe(
			(_res) => {
				this.CurrentUserService.setCurrentUser('N/A');
				localStorage.removeItem('authToken');
				this.Router.navigate(['']);
			},
			(_error) => { 
				console.log(_error);
			});
	}
	ngOnInit() {
		console.log('ahora');
		this.CurrentUserService.getCurrentUser().subscribe((currentUser)=>{
			this.appUser = currentUser;
		});
		console.log(this.appUser);
	}
}



