import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { LandingComponent } from '../landing/landing.component';
import { LoginComponent } from '../login/login.component';
import { RegisterComponent } from '../register/register.component';
import { HomeComponent } from '../home/home.component';

const appRoutes  = [
		{path: 'main', 	 component:LandingComponent, children:[
			{path: 'login',    component: LoginComponent, outlet: 'landingOutlet'},
			{path: 'register', component: RegisterComponent, outlet: 'landingOutlet'},
			{path: '', component: RegisterComponent, outlet: 'landingOutlet'}
		]},
		{path: 'home/:user',component: HomeComponent},
		{path: '', redirectTo:'main', pathMatch: 'full'},
		{path: '**', 		component: LandingComponent}
];

@NgModule({
	imports: [
		CommonModule,
		RouterModule.forRoot(appRoutes)
	],
	declarations: [],
	exports: [RouterModule]
})
export class AppRouterModule { }



