import { AfterViewInit, Component, OnInit, Input, ViewChild} from '@angular/core';
import { LoginService } from '../login.service';
import { CurrentUserService } from '../current-user.service';
import { Router } from '@angular/router';
import { ElementRef, Renderer2} from '@angular/core';
declare var jquery:any;
declare var $:any;

@Component({
  providers: [LoginService],
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
	data: {
		userName:	string, 
		userPwd:	string,
		rememberFlag:boolean
	}
	loginResponse: {
		userName:	string, 
		rememberFlag:boolean,
		loginResult:string,
		loginError: string,
		token:		Object
	}
	recoveryResponse: {
	userEmail:	string; 
	recoveryResult:string;
	recoveryError: string;
	}
	connectionError: string;
	recoveryMessage: string;
	recoveryMessageColor :string;
	showRecoveryResult: boolean;
	@ViewChild('inputEmailRecovery') inputEmailRecovery:ElementRef;
	constructor(private LoginService: LoginService, private CurrentUserService: CurrentUserService, private Router: Router) { 
		this.data = {	userName:'', 
						userPwd:'', 
						rememberFlag:false
					};
		this.loginResponse = {
						userName:'', 
						rememberFlag:false,
						loginResult:'',
						loginError:'',
						token:{}
					};
		this.recoveryResponse = {
						userEmail:'',
						recoveryResult:'',
						recoveryError: ''
					};
		this.connectionError = 'N/A';
		this.showRecoveryResult = true;
		this.recoveryMessage = 'N/A';
	}
	resetModal(){
		this.recoveryMessage = '';
		this.inputEmailRecovery.nativeElement.value = '';
	}
	showPopOver(input: string, message: string) {
		$(input).popover('dispose');
		$(input).popover({content:message, trigger:'manual'});
		$(input).popover('show');
	}
	getLoginData(userName:string, userPwd:string, rememberFlag:boolean){
		this.data.userName = userName;
		this.data.userPwd = userPwd;
		this.data.rememberFlag = rememberFlag;
		this.LoginService.sendData(this.data).subscribe(
			(_res) => {
				this.loginResponse = _res;				
			},
			(_error) => { 
				this.connectionError = 'Connection error, no server response';
				this.showPopOver('#loginButton', this.connectionError);
			},
			() => {
				if(this.loginResponse.loginResult =='LOGIN_OK') {
					localStorage.setItem('authToken', JSON.stringify(this.loginResponse.token));
					this.CurrentUserService.setCurrentUser(this.loginResponse.userName);
					this.Router.navigate(['/home',this.loginResponse.userName]);		
				}
				else this.showPopOver('#loginButton', this.loginResponse.loginError);
			});
	}
	sendRecovery (userMail:string){
		$('#recoveryButton').popover('dispose');
		this.LoginService.sendRecoveryMail(userMail).subscribe(
			(_res) => {
				this.recoveryResponse = _res;
			},
			(_error) => {
				this.connectionError = 'Connection error, no server response';
				this.showPopOver('#recoveryButton', this.connectionError);},
			() => {
				if(this.recoveryResponse.recoveryResult == 'RECOVERY_OK') {
					this.recoveryMessage = 'E-mail sent';
					this.recoveryMessageColor = 'green';
				}
				else {
					this.recoveryMessage = this.recoveryResponse.recoveryError;
					this.recoveryMessageColor = 'red';
				}
				this.showRecoveryResult = false;
			});
	}
	ngOnInit() {}
}



