import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable'
import { Http, Headers, RequestOptions, Response, RequestMethod } from '@angular/http'
import 'rxjs/add/operator/map';

class AuthResponse {
	userName: string;
	authResult: string;
	authError: string;
}

@Injectable()
export class AuthService {
	url: string;
	constructor(private http: Http) {
		this.url = 'http://127.0.0.1:11981/authenticator'
	}
	sendToken(token):Observable<AuthResponse>{
		let headers = new Headers({'Content-Type' : 'application/json'});
		let options = new RequestOptions({method: RequestMethod.Post, headers : headers , withCredentials : true});
		return this.http.post(this.url, {token: JSON.parse(token)}, options).map((_res)=> _res.json());
	}
}



